.veui-console .output .log:last-child::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 2px;
  background-color: #369;
}
.veui-console small {
  color: #999;
}
.veui-console.expanded .output {
  overflow: auto;
  height: 40vh;
}
